import React, { useState, useContext } from "react";
import ReactPaginate from "react-paginate";

import PaginationArrow from "../pagination-arrow/pagination-arrow";

import styles from "./paginated-items.module.css";

import LangContext from "../../../contexts/lang";

function PaginatedItems({
  itemsPerPage,
  children,
  controlsClassName,
  itemClassName,
}) {
  const langContext = useContext(LangContext);

  // Here we use item offsets; we could also use page offsets
  // following the API or data you're working with.
  const [itemOffset, setItemOffset] = useState(0);

  // Simulate fetching items from another resources.
  // (This could be items from props; or items loaded in a local state
  // from an API endpoint with useEffect and useState)
  const endOffset = itemOffset + itemsPerPage;
  console.log(`Loading items from ${itemOffset} to ${endOffset}`);
  const currentItems = children.slice(itemOffset, endOffset);
  const pageCount = Math.ceil(children.length / itemsPerPage);

  // Invoke when user click to request another page.
  const handlePageClick = (event) => {
    const newOffset = (event.selected * itemsPerPage) % children.length;
    console.log(
      `User requested page number ${event.selected}, which is offset ${newOffset}`
    );
    setItemOffset(newOffset);
  };

  return (
    <>
      {currentItems &&
        currentItems.map((item, i) => (
          <div
            key={i}
            className={itemClassName}>
            {item}
          </div>
        ))}
      <ReactPaginate
        className={`${styles.container} ${controlsClassName}`}
        breakLabel="..."
        nextLabel={
          <PaginationArrow isLeft={langContext.dir === "ltr" ? false : true} />
        }
        previousLabel={
          <PaginationArrow isLeft={langContext.dir === "ltr" ? true : false} />
        }
        onPageChange={handlePageClick}
        pageRangeDisplayed={5}
        pageCount={pageCount}
        pageClassName={styles.page}
        pageLinkClassName={styles.link}
        activeClassName={styles.active}
        renderOnZeroPageCount={null}
      />
    </>
  );
}

export default PaginatedItems;
